import * as React from "react"

import Layout from "../components/layout"
import { Link } from "gatsby";

const NotFoundPage = () => (
  <Layout>
    <h1 className="page-header">Страница не найдена</h1>
    <p className="text-lg">Попробуйте вернуться на <Link to="/" className="link-brand">Главную</Link></p>
  </Layout>
)

export default NotFoundPage
